<template>
  <BannerTop/>
  <div class="container">
    <div class="card card-primary">
      <div class="card-header">
        <h3 class="card-title">Наскрізний банер на задньому плані</h3>  
      </div>
      <div>
        <div class="card-body">
          <div class="seamless-input">
            <div>
              <label class="imageRadio">
                <input type="radio" v-model="selectedOption" value="image" />
                Фото на фон
              </label>
              <div v-if="selectedOption === 'image'">
                <input type="file" ref="fileRadio" class="input-radio" @change="fileRagioBack">
                <button @click="radioFileInput" class="btn btn-baner btn-default" v-if="!imageRadio">Добавити</button>
                <div class="divRadio">
                  <img :src="imageRadio" class="imgInput" v-if="imageRadio" />
                  <button @click="deleteImageRadio" class="btn btn-baner btn-default" v-if="imageRadio">Видалити</button>
                </div>
              </div>
            </div>
            <div>
              <label class="imageRadio">
                <input type="radio" v-model="selectedOption" value="color" />
                Просто фон
              </label>
              <div v-if="selectedOption === 'color'">
                <label :for="'color-input'">
                  Колір:
                  <input type="color" v-model="selectedColor" :id="'color-input'" />
                </label>
                <div :style="{ backgroundColor: selectedColor, width: '100px', height: '100px' }"></div>
              </div>
            </div>
          </div>
        </div> 
        <div v-if="showMessage" class="message">
          {{ message }}
        </div>
        <div class="radio-footer">
            <button @click="saveSeamless" class="btn btn-primary" type="submit" :disabled="selectedOption === ''">Зберегти</button>
          </div>
      </div>
    </div>
  </div>
  <BannerNews/>
</template>
  <script>
  
  import BannerTop from '@/components/BannerTop.vue'
  import BannerNews from '@/components/BannerNews.vue'

  import { getDatabase, ref as dbRef, set, push, update, onValue, remove, get} from 'firebase/database'
  import { getStorage, ref as storRef, uploadBytes, put, getDownloadURL, deleteObject, listAll } from 'firebase/storage'

  export default {
  
      data(){
        return{
          selectedOption: '',
          selectedColor: '#000000',
          imageRadio: '',
          fileImageRadio: '',
          message: 'Збережено!',
          showMessage: false,
        }
      },

      components: {
        BannerTop,
        BannerNews,
      },

      methods: {
        fileRagioBack(event){
          const file = event.target.files[0];
          this.fileImageRadio = file
          this.imageRadio = URL.createObjectURL(file);
        },
        radioFileInput(){
          this.$refs.fileRadio.click();
        },
        async deleteImageRadio(){
          console.log(this.banners.seamless.imageSeamless)
          if(this.banners.seamless.imageSeamless === undefined){
            this.imageRadio = ''
          } else{
            const db = getDatabase();
            const pathDb = `banners/seamless/imageSeamless`
            const itemRef = dbRef(db, pathDb);
    
            await remove(itemRef)
              .then(() => {
                console.log("Item successfully deleted from Realtime Database");
              })
              .catch((error) => {
                console.log("Error deleting item from Realtime Database: ", error);
              });
            }

        },

        saveSeamless(){
          const db = getDatabase()
          const storage = getStorage()

          const path = `banners/seamless`
          const moviesRef = dbRef(db, path);

          if (this.selectedOption === 'image') {
            const storageRef = storRef(storage, `banners/seamless/imageSeamless`);
            uploadBytes(storageRef, this.fileImageRadio).then((snapshot) => {
              getDownloadURL(snapshot.ref).then((url) => {
                update(moviesRef, {
                  select: this.selectedOption,
                  imageSeamless: url
                });
              });
            });
          } else if(this.selectedOption === 'color'){
            set(moviesRef, {
              select: this.selectedOption,
              color: this.selectedColor,
            });
          }

          this.showMessage = true
          setTimeout(() => {
              this.showMessage = false
          }, 3000)

        },
        async getSeamless() {
          const db = getDatabase()
          const bannerRef = dbRef(db, 'banners');
          await new Promise((resolve) => {
            onValue(bannerRef, (snapshot) => {
              if (snapshot.exists()) {
                this.banners = snapshot.val();
                this.selectedOption = this.banners.seamless.select
                if(this.selectedOption === 'image'){
                  this.imageRadio = this.banners.seamless.imageSeamless
                } else{
                  this.selectedColor = this.banners.seamless.color
                }
              } else {
                console.log('Баннер не знайдено');
              }
              resolve();
            });
          });
        },
      },

      async mounted() {
        await this.getSeamless();
      },
    
  }
  </script>

  <style>

  .seamless-input{
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .imageRadio{
    display: flex;
    gap: 10px;
  }

  .input-radio{
    position: absolute;
    top: 2rem;
    left: 0;
    opacity: 0;
    width: 0px;
    cursor: pointer;
    height: 38px;
  }

  .divRadio{
    display: flex;
    gap: 20px;
    align-items: end;
  }

  .radio-footer{
    display: flex;
    justify-content: center;
    padding: 0.75rem 1.25rem;
    background-color: rgba(0,0,0,.03);
    border-top: 0 solid rgba(0,0,0,.125);
  }

  </style>