<template>
    <div class="container">
    <div class="card card-primary">
      <div class="card-header">
        <h3 class="card-title">На головній сторінці зверху</h3>  
        <div class="panel-body">  
          <label class="switch">
            <input type="checkbox" v-model="checkboxTopBanner" @click="toggleCheckbox">
            <div class="slider round"></div>
          </label>
        </div>
      </div>
      <div>
        <div class="card-body">
          <div class="file-input">
              <input type="file" ref="fileInput2" class="input-baner" @change="handleFileChange" multiple>
              <button @click="triggerFileInput" class="btn btn-baner btn-default" v-if="banerTop.length == 0">Добавити</button>
              <div class="delete-img" v-if="banerTop.length >= 1">
                <div class="image-delete" v-for="(banerTopImg, i) in banerTop" :key="i">
                  <img :src="banerTopImg.url" class="imgInput" v-if="banerTopImg">
                  <span class="deleteSpan" v-if="banerTopImg" @click="deleteBannerTop(i)">x</span>
                </div>
                <button @click="triggerFileInput" class="btn btn-block btn-default"  v-if="banerTop.length >= 1">Добавити</button>
              </div>
          </div>
          <!-- <div class="file-input" v-if="doneTop === true">
              <input type="file" ref="fileInput22" class="input-baner" @change="handleFileChange" multiple>
              <div class="delete-img">
                <div class="image-delete" v-for="(banerTopImg, i) in banerTop" :key="i">
                  <img :src="banerTopImg" v-if="banerTopImg" class="imgInput">
                  <span class="deleteSpan" v-if="banerTopImg" @click="deleteBannerTop(i)">x</span>
                </div>
                <button @click="triggerFileInput" class="btn btn-block btn-default">Добавити2</button>
              </div>
          </div> -->
        </div> 
        <div v-if="showMessage" class="message">
          {{ message }}
        </div>
        <div class="card-footer">
          <div class="col-sm-6">
            <div class="form-group">
              <label>Швидкість обертання</label>
              <select class="form-control" v-model="speedTopBanner">
                <option value="1">1 сек</option>
                <option value="2">2 сек</option>
                <option value="3">3 сек</option>
                <option value="4">4 сек</option>
                <option value="5">5 сек</option>
              </select>
            </div>
          </div>
          <button @click="saveBaner" class="btn btn-primary" type="submit" :disabled="banerTop.length === 0">Зберегти</button>
        </div>
      </div>
    </div>
  </div>
  </template>
  <script>
  
  import { getDatabase, ref as dbRef, set, push, update, onValue, remove, get} from 'firebase/database'
  import { getStorage, ref as storRef, uploadBytes, put, getDownloadURL, deleteObject, listAll } from 'firebase/storage'
  
  export default {
  
      data(){
        return{
          checkboxTopBanner: false,
          speedTopBanner: 1,
          banners: {},
          banerTop: [],
          fileBanerTop: [],
          message: 'Збережено!',
          showMessage: false,
          doneTop: false,
        }
      },
  
      methods:{
        handleFileChange(event) {
          this.fileBanerTop = [...event.target.files]
          for(let image of this.fileBanerTop){
            const imageSrc = URL.createObjectURL(image)
            this.banerTop.push({
              url: imageSrc,
            })
          }
        },
        triggerFileInput(){
          this.$refs.fileInput2.click();
        },
        deleteBanerTop(i){
          this.banerTop.splice(i, 1)
          this.fileBanerTop.splice(i, 1)
        },
        async deleteBannerTop(i){

          let baner;

          this.banerTop.forEach((banner, index) => {
            if(index === i){
              baner = banner
            } 
          });

          this.banerTop.splice(i, 1)
          this.fileBanerTop.splice(i, 1)

        //Storage
          const storage = getStorage();
          const pathStr = `banners/bannerTop/${baner.name}`
          const fileRef = storRef(storage, pathStr);
          try {
            await deleteObject(fileRef);
            console.log(`Файл з індексом ${baner.name} успішно видалено`);
          } catch (error) {
            console.error(`Помилка видалення файлу з індексом ${baner.name}:`, error);
          }
          //BD
          const db = getDatabase();
          const pathDb = `banners/bannerTop/image/${i}`
          const itemRef = dbRef(db, pathDb);
  
          await remove(itemRef)
            .then(() => {
              console.log("Item successfully deleted from Realtime Database");
            })
            .catch((error) => {
              console.log("Error deleting item from Realtime Database: ", error);
            });
        },
        async saveBaner(){
          const db = getDatabase()
          const storage = getStorage()
          const path = 'banners/bannerTop'
          const moviesRef = dbRef(db, path)

          // set(moviesRef, {
            
          // })

          const snapshot = await get(moviesRef)
          const oldBannerTop = (snapshot.val() && snapshot.val().image) || []
          const newBannerTop = [...oldBannerTop]

          if(this.fileBanerTop !== []){
            await Promise.all(this.fileBanerTop.map(async (image) => {
            const name = 'bannerTop-' + Date.now() + Math.floor(Math.random() * 1000);
            const storageRef = storRef(storage, `banners/bannerTop/${name}`);
            await uploadBytes(storageRef, image);
            const url = await getDownloadURL(storageRef);
            newBannerTop.push({
              name: name,
              url: url,
            });
          }));

        }
          console.log(newBannerTop);
          update(moviesRef, {
            checked: this.checkboxTopBanner,
            speed: this.speedTopBanner,
            image: newBannerTop,
          });
          
          
        this.fileBanerTop = []

        this.showMessage = true
          setTimeout(() => {
              this.showMessage = false
          }, 3000)

        },
        async getBanners() {
          const db = getDatabase()
          const bannerRef = dbRef(db, 'banners');
          await new Promise((resolve) => {
            onValue(bannerRef, (snapshot) => {
              if (snapshot.exists()) {
                this.banners = snapshot.val();
                this.doneTop = true
                if (typeof this.banners.bannerTop.image !== 'undefined' && Array.isArray(this.banners.bannerTop.image)) {
                  this.banerTop = this.banners.bannerTop.image
                } else {
                  this.banerTop = [];
                }
                //this.banerTop = this.banners.bannerTop.image
                this.checkboxTopBanner = this.banners.bannerTop.checked
                this.speedTopBanner = this.banners.bannerTop.speed
                //this.fileBanerTop = this.banners.banerTop
              } else {
                console.log('Баннер не знайдено');
                this.doneTop = false
              }
              resolve();
            });
          });
        },
        toggleCheckbox() {
          this.checkboxTopBanner = !this.checkboxTopBanner
          this.$emit('setCheckboxVal', this.checkboxTopBanner)
        },
      },
  
      async mounted() {
        await this.getBanners();
      },
    
  }
  </script>
  
  
  <style lang="scss">
  
  .container{
    width: 70%;
  }

  .card-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  
  .upload-button {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 6px 12px;
  cursor: pointer;
  }
  
  .upload-preview {
  display: inline-block;
  width: 100px;
  height: 100px;
  border: 1px solid #ccc;
  margin-left: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  }
  label{
  display: block;
  text-align: left;
  }
  .file-input {
  position: relative;
  }
  .input-baner {
    position: absolute;
    top: 0.5rem;
    left: 0;
    opacity: 0;
    width: 30%;
    cursor: pointer;
    height: 38px;
  }
  .btn-baner {
    display: block;
    width: 30%;
    margin: 0.5rem 0;
  }
  .btn-block {
    display: block;
    width: 30%;
  }
  .imgInput{
  width: 250px;
  height: 250px;
  display: block;
  }
  .image-delete_btn{
  display: flex;
  gap: 40px;
  align-items: end;
  margin-bottom: 0.5rem;
  }
  .delete-img{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: end;
  margin-bottom: 0.5rem;
  }
  .deleteSpan{
  position: absolute;
  top: -8px;
  right: -8px;
  border: 1px solid black;
  background: white;
  border-radius: 50%;
  color: black;
  width: 26px;
  cursor: pointer;
  }
  .image-delete{
  position: relative;
  }
  .form-main{
  display: flex;
  gap: 20px;
  }
  .form-check-input {
  display: block;
  }
  .form-group{
  
  h5{
    text-align: start;
  }
  }
  .card-footer{
  display: flex;
  gap: 30px;
  justify-content: flex-start;
  padding-left: 5rem;
  }
  .col-sm-6 {
    max-width: 43%;
  }
  .form-group {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-bottom: 0;
  }
  label:not(.form-check-label):not(.custom-file-label) {
    font-weight: 400;
  }
  label{
    margin-bottom: 0;
  }
  .form-control {
    width: 100px;
  }  

  //SWITCH
  .panel-body{
    position: absolute;
    right: 1.25rem;
  }

  .switch {
  position: relative;
  display: inherit;
  width: 50px;
  height: 24px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #3eee08;
}

input:focus + .slider {
  box-shadow: 0 0 1px #3eee08;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
    
  </style>
  